import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import First from '@/components/First'
import Second from '@/components/Second'
import NeedWechat from '@/components/NeedWechat'
import Test from '@/components/Test'

// 获取访问的user-agent
let ua = navigator.userAgent.toLowerCase() || window.navigator.userAgent.toLowerCase()
    // 判断user-agent
export const platform = {
    isApp: /creditaccelerator/i.test(ua), // APP
    isWX: /MicroMessenger/i.test(ua), // 微信端，（排除掉现金巴士app&好人好信app的坑）
    isIOS: /i(Phone|Pad|Pod|OS)/i.test(ua), // 苹果家族
    isAndroid: /(android|nexus)/i.test(ua), // 安卓家族
    isWindows: /(Windows Phone|windows[\s+]phone)/i.test(ua), // 微软家族
    isBlackBerry: /BlackBerry/i.test(ua), // 黑莓家族
}

Vue.use(Router)
const isFree = true
const ROUTER = new Router({
    routes: [{
        path: '/',
        name: 'Index',
        component: Index
    }, {
        path: '/first',
        name: 'First',
        component: First
    }, {
        path: '/sec',
        name: 'Second',
        component: Second
    }, {
        path: '/test',
        name: 'test',
        component: Test,
        meta: {
            isFree
        }
    }, {
        path: '/needWeixin',
        name: 'needWeixin',
        component: NeedWechat,
        meta: {
            isFree
        }
    }]
})
ROUTER.beforeEach((to, from, next) => {
    const { title, isFree, inFlow } = to.meta
        // if (!isFree) {
        //     unAuthRedirect(to)
        //     return
        // }
    next()
})
export default ROUTER
export function unAuthRedirect(to) {
    console.log(platform.isWX)
    if (platform.isWX) {
        let appid = 'wxd77ed25321d29bad'
        const redirect_uri = encodeURIComponent([location.origin, `/weixin/oauth2?returnUrl=`, encodeURIComponent(`${location.origin}/#${to.fullPath}`)].join(''))
        location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appid}&redirect_uri=${redirect_uri}&response_type=code&scope=snsapi_base&state=creditLogin#wechat_redirect`
    } else {
        ROUTER.push({ name: 'needWeixin' })
    }
}


