<template>
  <div class="index">
    <transition name="fade">
        <div class="loading" v-show="load<100">
            <p class="txt">2020<br>你的新春祝福<br>马上上桌</p>
            <img src="~@/assets/images/box@2x.png" alt="">
            <div class="progress">
                <div class="actual" :style="{width:load+'%'}">
                <div class="bar">{{load}}%</div>
                </div>
            </div>
        </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
      msg: '',
      load: 0,
      first:true
    }
  },
  created(){
    var timer = setInterval(()=> {
        this.load = parseInt(this.load + Math.random() * 10, 10);
        if (this.load > 90) {
            this.load = 100;
            clearInterval(timer);
            setTimeout(()=>{
                this.$router.push('first')
            },0)
        }
    }, 500);
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.loading{
  position: absolute;
  background-color: #E7351F;
  background-image: url('~@/assets/images/loading@2x.png');
  width: 375px;
  height: 100%;
  position: absolute;
  background-size: cover;
  .txt{
    font-family: NotoSerifCJKscBlack;
    text-align: center;
    color: #fff;
    font-size: 18px;
    line-height: 25px;
    top: 156px;
    position: absolute;
    width: 100%;
  }
  img{
    width: 154px;
    position: absolute;
    top:240px;
    left: 50%;
    margin-left: -77px;
  }
  .progress{
    width: 304px;
    height: 25px;
    background-image: url('~@/assets/images/pro-bg@2x.png');
    background-size: contain;
    position: absolute;
    top: 424px;
    left: 50%;
    margin-left: -152px;

    .actual{
        width: 20px;
        height: 20px;
        background-size: auto 23px;
        position: absolute;
        left: 0;
        top: 1px;
        background-color: #FDC7A1;
        border-radius: 10px;
        box-shadow: 0 1px 3px 0 rgba(39, 32, 20, 0.18);
        transition: 0.1s;

      .bar{
        width: 137px;
        height: 62px;
        background-image: url('~@/assets/images/pro-cloud@2x.png');
        background-size: contain;
        position: absolute;
        top: -21px;
        right: -69px;
        color: #3d3d3d;
        text-align: right;
        line-height: 62px;
        padding-right: 25px;
        font-family: NotoSerifCJKscBlack;
        font-size: 18px;
      }
    }
  }
}
.first{
    background-image: url('~@/assets/images/sec@2x.png');
    width: 375px;
    height: 100%;
    position: absolute;
    background-size: contain;

    img{
        position: absolute;
    }

    .txt2{
        width: 68px;
        height: 107.5px;
        position: absolute;
        left: 36px;
        top:40px;
    }
    .txt3{
        width: 53.5px;
        height: 17.5px;
        position: absolute;
        left: 72px;
        top:340px;
    }
    .txt4{
        width: 146.5px;
        height: 96px;
        position: absolute;
        left: 36px;
        bottom:51px;
    }
    .box2{
        width: 288px;
        height: 326px;
        top:187px;
        position: absolute;
        left: 43.5px;
    }

    .letter{
        width: 108px;
        height: 124.5px;
        position: absolute;
        top:287.5px;
        left: 107.5px;
    }
    .whitedot{
        position: absolute;
        width: 24px;
        height: 24px;
        background: #fff;
        border-radius: 24px;
        top: 339px;
        left: 147px;
        opacity: 0.6;
    }
    .whitedot2{
        position: absolute;
        width: 12px;
        height: 12px;
        background: #fff;
        border-radius: 12px;
        top: 345px;
        left: 153px;
    }
    .c1{
        width: 155px;
        height: 100.5px;
        left: 0;
        top:151px;
    }
    .c2{
        width: 148.5px;
        height: 84.5px;
        right: 0;
        top:221.5px;
    }
    .c3{
        width: 117px;
        height: 106.5px;
        left: 0;
        top:378.5px;
    }
    .c4{
        width: 174.5px;
        height: 123px;
        right: 0;
        top:422.5px;
    }

}
</style>



