<template>
  <div id="app">
    <router-view/>
    <img src="~@/assets/images/close@2x.png" v-show="!play" class="contr" @click="musicPause" alt="">
    <img src="~@/assets/images/open@2x.png" ref="playbtn" v-show="play" class="contr" @click="musicPlay" alt="">
    <audio class="media-audio" loop muted controls ref="MusicPlay">
        <source src="~@/assets/images/music.mp3" type="audio/mpeg">
    </audio>
  </div>
</template>

<script>
export default {
    name: 'App',
    data(){
        return {
            play:true
        }
    },
    // mounted(){

    //     let self=this
    //     document.addEventListener('DOMContentLoaded', function () {
    //         self.musicPlay()
    //     });
    // },
    methods:{
        musicPause() {
            this.$nextTick(() => {
                this.$refs.MusicPlay.pause();
                this.play = true
            })

        },
        musicPlay() {
            this.$nextTick(() => {
                this.$refs.MusicPlay.play();
                this.play = false
            })
        }
    }
}
</script>

<style>
@font-face {
    font-family: NotoSerifCJKscBlack;
    src: url('./assets/fonts/NotoSerifCJKsc-Black.otf')
}
@font-face {
    font-family: NotoSerifCJKscLight;
    src: url('./assets/fonts/NotoSerifCJKsc-Light.otf')
}
@font-face {
    font-family: NotoSerifCJKscRegular;
    src: url('./assets/fonts/NotoSerifCJKsc-Regular.otf')
}
.contr{
    width: 82px;
    height: 82px;
    position: fixed;
    right: 0;
    top: 14.5px;
}
.media-audio{
    position: absolute;
    position: absolute;
    z-index: -2;
    opacity: 0;
}
</style>



